import { auth, signInWithEmailAndPassword } from "@/firebase";
import router from "@/router";

import apiClient from "@/store/core/apiClient";

export default (app, customer, vendor) => {
	const setUser = async (uid) => {
		try {
			const { data } = await apiClient.get(`/get-user?uid=${uid}`);

			let account;

			if (data.account == "customer") {
				account = data.account;
				delete data.account;
				Object.assign(customer, data);
			}

			if (data.account == "vendor") {
				account = data.account;
				delete data.account;
				Object.assign(vendor, data);
			}

			app.isLoggedin = true;
			console.info("Logged In");

			if (account == "customer") router.push("/u");
			if (account == "vendor") router.push("/a");
		} catch (error) {
			console.error(error);
		}
	};

	const login = async (email, password) => {
		console.info("Logging In...");

		// Sign In With Email & Password
		try {
			let { user } = await signInWithEmailAndPassword(auth, email, password);

			setUser(user.uid);

			return true;
		} catch (error) {
			console.error(error);
			return false;
		}
	};

	const registerCustomer = async (customer) => {
		delete customer.confirmPassword;
		try {
			const { data } = await apiClient.post("/register-customer", customer);
			console.info("Customer registered");

			if (data) return true;
		} catch (error) {
			console.error(error.response.data);
		}
	};

	const registerVendor = async (vendor) => {
		delete vendor.confirmPassword;

		try {
			const { data } = await apiClient.post("/register-vendor", vendor);
			console.info("Vendor registered");

			if (data) return true;
		} catch (error) {
			console.error(error.response.data);
		}
	};

	return { setUser, login, registerCustomer, registerVendor };
};
