const routes = [
	{
		path: "/",
		name: "app",
		component: () => import("../layouts/app/Main.vue"),
		children: [
			{
				path: "/home",
				name: "home",
				component: () => import("../pages/Home.vue"),
			},
		],
	},
	{
		path: "/u",
		name: "customer",
		component: () => import("../layouts/customer/Main.vue"),
	},
	{
		path: "/a",
		name: "vendor",
		component: () => import("../layouts/vendor/Main.vue"),
	},
	{
		path: "/d",
		name: "delivery",
		component: () => import("../layouts/delivery/Main.vue"),
	},
	{
		path: "/about",
		redirect: "/about/privacy-policy",
		children: [
			{
				path: "privacy-policy",
				component: () => import("../pages/about/PrivacyPolicy.vue"),
			},
			{
				path: "terms-of-service",
				component: () => import("../pages/about/TermsOfService.vue"),
			},
		],
	},
	{
		path: "/auth",
		component: () => import("../layouts/auth/Auth.vue"),
		redirect: "/auth/login",
		children: [
			{ path: "register", component: () => import("../pages/auth/Register.vue") },
			{
				path: "login",
				component: () => import("../pages/auth/Login.vue"),
			},
		],
	},
	{
		path: "/:catchAll(.*)*",
		component: () => import("../pages/Error.vue"),
	},
];

export default routes;
